/*Reset*/
html, body{
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  color: #1f4568;

    scroll-behavior: smooth;

}


/* Overlay for background dim */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  zoom:150%;
}

/* Popup container */
.popup {
  background: white;
  border-radius: 10px;
  padding: 20px;
  width: 300px;
  max-width: 90%;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  position: relative;
  text-align: center;
}

/* Close button */
.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  color: #999;
}
.close:hover {
  color: #333;
}

/* Heading */
.popup h2 {
  margin-top: 0;
  color: #333;
  font-size: 1.5em;
}

/* Contact number */
.popup-call-link {
  display: inline-block;
  margin-top: 15px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  font-weight: bold;
  border-radius: 5px;
  text-decoration: none;
  transition: background 0.3s;
}
.popup-call-link:hover {
  background-color: #0056b3;
}
.footer {
  padding: 10px 0;
}

.footer .container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.footer .contact {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px; /* Adds spacing between icons */
}

.footer__social-l img {
  width: 54px;
  height: 54px;
}

/* Responsive styling for screens 600px or smaller */
@media (max-width: 600px) {
  .footer .contact {
    flex-direction: row;
    gap: 10px; /* Reduce spacing between icons */
  }

  .footer__social-l {
    transform: scale(1.5); /* Adjust scale to fit smaller screens */
  }
}


.header__img{
